import React, { useState } from 'react'
import bem from 'utils/bem'
import { IProductItem } from 'interface'
import './style.scss'

type Props = {
  data: IProductItem
  selectedObject: number
  selectObject: (value: number) => void
  index: number
}

const b = bem('product-item')

function ProductItem({ data, index, selectObject, selectedObject }: Props) {
  const [isOnHover, setIsOnHover] = useState(false)

  const handleMouseEnter = () => {
    setIsOnHover(true)
  }

  const handleMouseLeave = () => {
    setIsOnHover(false)
  }

  const handleClick = () => {
    console.log(111)
    const mapElement = document.getElementById('map')
    mapElement?.scrollIntoView({ behavior: 'smooth' })
    selectObject(index)
  }

  return (
    <div className={b('', { selected: selectedObject === index })} id={`product-item-${index}`}>
      <span className={b('name', { selected: selectedObject === index })}>{data.name}</span>
      <div
        className={b('image-block')}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        <img className={b('image', { cursor: true })} src={data.imageUri} alt="img" />
        {isOnHover && (
          <div className={b('vual', { show: isOnHover })}>
            <button className={b('vual-btn')} type="button" onClick={handleClick}>
              Показать на карте
            </button>
          </div>
        )}
      </div>
      <span className={b('description', { selected: selectedObject === index })}>
        {data.description}
      </span>
    </div>
  )
}

export default ProductItem
