import React from 'react'
import bem from 'utils/bem'
import { IProductItem } from 'interface'
import mainIamgeMap from 'img/main-image-map.png'
import './style.scss'

const b = bem('interective-map')

type Props = {
  list: IProductItem[]
  selectedObject: number
  selectObject: (value: number) => void
}

function InterectiveMap(props: Props) {
  const { list, selectObject, selectedObject } = props

  const getIconObject = (object: IProductItem) => {
    switch (object.type) {
      case 'monument':
        return 'https://naturerussia.travel/local/frontend/src/theme/images/icon2.png'
      case 'reserve':
        return 'https://naturerussia.travel/local/frontend/src/theme/images/icon1.png'
      case 'park':
        return 'https://naturerussia.travel/local/frontend/src/theme/images/icon3.png'
      default:
        return ''
    }
  }

  const handleClick = (object: IProductItem, index: number) => {
    if (index === selectedObject) {
      selectObject(0)
      return
    }
    const mapElement = document.getElementById(`product-item-${index}`)
    const top =
      (mapElement?.clientHeight || 0) +
      (mapElement?.offsetTop || 0) +
      (mapElement?.clientHeight || 0) / 2
    window.scrollTo({ top, behavior: 'smooth' })
    selectObject(index)
  }

  return (
    <div className={b('')}>
      <div className={b('background-block')}>
        <img
          className={b('background-image')}
          src="https://naturerussia.travel/local/frontend/src/theme/images/webp/bg--main-top.webp"
          alt="img"
        />
        <img
          className={b('background-image')}
          src="https://naturerussia.travel/local/frontend/src/theme/images/webp/bg--main-top.webp"
          alt="img"
        />
        <img
          className={b('background-image')}
          src="https://naturerussia.travel/local/frontend/src/theme/images/webp/bg--main-top.webp"
          alt="img"
        />
        <img
          className={b('background-image')}
          src="https://naturerussia.travel/local/frontend/src/theme/images/webp/bg--main-top.webp"
          alt="img"
        />
        <img
          className={b('background-image')}
          src="https://naturerussia.travel/local/frontend/src/theme/images/webp/bg--main-top.webp"
          alt="img"
        />
        <img
          className={b('background-image')}
          src="https://naturerussia.travel/local/frontend/src/theme/images/webp/bg--main-top.webp"
          alt="img"
        />
      </div>
      <div className={b('map-container')} id="map">
        <img
          className={b('map-image')}
          src={mainIamgeMap}
          alt="img"
        />
        {list &&
          list.length &&
          list.map((item: IProductItem, index) => (
            <div
              className={b('icon-block')}
              style={{
                top: item.top,
                left: item.left,
              }}
              onClick={() => {
                handleClick(item, index + 1)
              }}
              role="button"
              tabIndex={0}
            >
              <img
                className={b('icon', { selected: selectedObject === index + 1 })}
                src={getIconObject(item)}
                alt={item.name}
                title={item.name}
              />
            </div>
          ))}
      </div>
    </div>
  )
}

export default InterectiveMap
